<template>
  <div class="cooperation-list" v-loading="loading">
    <div class="panel" style="margin-bottom: 9px;">
      <div class="name">姓名</div>
      <div class="phone">联系方式</div>
      <div class="message">留言</div>
    </div>
    <div class="panel" v-for="(item, index) in dataArray" :key="index">
      <div class="name">{{item.name}}</div>
      <div class="phone">{{item.phone}}</div>
      <div class="message">{{item.note}}</div>
    </div>
    <el-pagination
      background
      layout="->, prev, pager, next"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
import { fetchCooperationInfo } from '@/api/tkpro'
import tk_logo from '@/assets/images/tk_logo.jpg';
import SingleUpload from '@/components/Upload/singleUpload'
export default {
  name: 'cooperation-list',
  components: {
    SingleUpload
  },
  data() {
    return {
      tk_logo,
      loading: false,
      name: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dataArray: [],
    }
  },
  created(){
    this.loadData()
  },
  methods:{
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      currentPage = val
      this.loadData()
      console.log(`当前页: ${val}`);
    },
    addGoods () {
      this.$router.push({path:'/pms/addProduct'});
    },
    loadData () {
      this.loading = true
      fetchCooperationInfo(this.name, this.currentPage).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.dataArray = res.data.list
        this.total = res.data.total
        // this.pageSize = res.data.pageSize
        console.log(JSON.parse(JSON.stringify(this.dataArray)));
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.cooperation-list {
  background: #eeeeee;
  padding: 20px;
  .panel {
    margin-bottom: 20px;
    display: flex;
    padding: 10px;
    display: flex;
    align-items: center;
    background-color: #fff;
    border: 1px solid #bbb;
    .name {
      width: 14%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
    }
    .phone {
      width: 21%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
    }
    .message {
      width: 65%;
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
      display: flex;
      align-items: center;
    }
  }
}
</style>



