<template>
  <div>
    <el-upload action="https://tuan.taokepro.cn/minio/img/upload" :headers="myHeaders" :on-success="handleSuccess" list-type="picture-card">
      <i slot="default" class="el-icon-plus"></i>
      <div slot="file" slot-scope="{file}">
        <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" >
        <span class="el-upload-list__item-actions">
          <span class="el-upload-list__item-preview" @click="handlePictureCardPreview(file)">
            <i class="el-icon-zoom-in"></i>
          </span>
          <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleDownload(file)">
            <i class="el-icon-download"></i>
          </span>
          <span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
            <i class="el-icon-delete"></i>
          </span>
        </span>
      </div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>
<script>

import { uploadImg } from '@/api/tkpro'
import { getToken } from '@/utils/auth'
export default {
  name: 'singleUpload',
  props: {
  },
  computed: {
  },
  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      myHeaders: {Authorization: getToken()}
    };
  },
  methods: {
    handleSuccess(res, file) { // action 上传时的回调
      if (res.code == '200') {
        this.$emit('uploadCallback', {
          url: res.data[0].url,
          name: res.data[0].name
        })
        this.$message.success(res.message || '文件导入成功，稍后查询处理结果');
      } else {
        this.$message.error(res.message || '文件导入失败，稍后重试');
      }
    },
    upload () {
      uploadImg().then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.typeList = res.data
      }).catch(error => {
        console.log(error);
      })
    },
    handleRemove(file) {
      console.log(file);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleDownload(file) {
      console.log(file);
    },

  }
}
</script>
<style>

</style>





